.header_outer_container {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    height: 10vh;
    color: white;
    transition: all .5s;
    transition-timing-function: ease-in;
    z-index: 1;
    width: 100%;
    position: fixed;
    /* background-color: rgb(20, 20, 20); */
}
.nav_black{
    /* background-color: rgb(20, 20, 20); */
    background-color: rgba(0, 0, 0, 0.2);
    -webkit-backdrop-filter: blur(15px);
    backdrop-filter: blur(15px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.header_container{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 35px;
}

.header_left ul{
    width: 140%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
}
.header_right ul{
    width: 100%;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    align-items: center;
    list-style: none;
}
.header_left ul li{
    cursor: pointer;
}
.header_right ul li{
    margin-left: 35px;
    cursor: pointer;
}

@media (max-width: 768px) {
  .header_outer_container .header_left .list {
    display: none;
  }
}